:root {
  --snow-cloud: #f1f1f2;
  --varm-grey: #bcbabe;
  --blue-ice: #a1d6e2;
  --glacier: #1995ad;
}

*, :after, :before {
  box-sizing: border-box;
}

body {
  background-color: var(--snow-cloud);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0;
}

.header {
  width: 100%;
  background-color: var(--glacier);
  padding: 24px 0;
  position: fixed;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .24);
}

.search-form {
  max-width: 400px;
  border: 4 solid var(--glacier);
  box-shadow: none;
  border-radius: 15px;
  justify-content: center;
  gap: 0;
  margin: auto;
  transition: box-shadow 1s cubic-bezier(.075, .82, .165, 1);
  display: flex;
  overflow: hidden;
}

.search-form:hover, .search-form:focus {
  transition: box-shadow 1s cubic-bezier(.075, .82, .165, 1);
  box-shadow: 0 6px 16px rgba(0, 0, 0, .24);
}

.input {
  color: #2f4f4f;
  width: 88%;
  border: none;
  outline: none;
  padding: 16px 0 16px 16px;
  font-size: 24px;
}

.btn {
  width: 12%;
  background-color: var(--blue-ice);
  cursor: pointer;
  fill: var(--glacier);
  border: none;
  padding: 16px 0;
  transition: fill .5s cubic-bezier(.075, .82, .165, 1);
}

.btn:hover, .btn:focus {
  fill: var(--snow-cloud);
  transition: fill .5s cubic-bezier(.075, .82, .165, 1);
}

